<template>
  <div class="newContractModal">
    <h1>{{ title }}</h1>
    <vue-form :state="formstate" @submit.prevent="onSubmit">
      <validate tag="label">
        <span>ContractNaam *</span>
        <input v-model="model.name" required name="name" />
        <br />
        <span>Contract</span>
        <multiselect
          name="files"
          v-model="model.file"
          :options="files"
          ref="multiSelectContacts"
          :multiple="false"
          :close-on-select="false"
          :clear-on-select="false"
          placeholder="Kies contract"
          label="name"
          track-by="name"
        ></multiselect>
        <br />
        <span>Categorie</span>
        <multiselect
          name="category"
          v-model="model.category"
          :options="categories"
          ref="multiSelectContacts"
          :multiple="false"
          :close-on-select="false"
          :clear-on-select="false"
          placeholder="Kies categorie"
          label="name"
          track-by="name"
        ></multiselect>
        {{ model.category }}
        <br />
        <span>Contacten</span>
        <multiselect
          name="contacts"
          v-model="model.contacts"
          :options="contacts"
          ref="multiSelectContacts"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          placeholder="Kies de contact(en)"
          label="name"
          track-by="name"
        >
          <template slot="item" slot-scope="data" v-if="data.length > 0">
            <span class="multiselect__single">{{ data }}</span>
          </template>
          <template
            slot="selection"
            slot-scope="{ values, data, search, isOpen }"
            v-if="values.length &amp;&amp; !isOpen"
          >
            <span class="multiselect__single"
              >{{ values.length }} contacten geselecteerd
            </span>
          </template>
        </multiselect>
        <br />
        <span>Notificaties in dagen</span>
        {{ model.daysBeforeNotification }}
        <multiselect
          name="daysbeforenotification"
          v-model="model.daysBeforeNotification"
          :options="daysBeforeNotifications"
          :multiple="true"
          label="name"
          :close-on-select="false"
          :clear-on-select="false"
          placeholder="Kies de notificatie dag(en)"
        >
          <template slot="selection" slot-scope="{ values, search, isOpen }"
            ><span
              class="multiselect__single"
              v-if="values.length &amp;&amp; !isOpen"
              >{{ values.length }} dagen geselecteerd</span
            ></template
          >
        </multiselect>
        <br />
        <span>Bedrijf*</span>
        <multiselect
          name="customers"
          v-model="model.customer"
          :options="customers"
          ref="multiSelectContacts"
          :multiple="false"
          :close-on-select="false"
          :clear-on-select="false"
          placeholder="Kies bedrijf"
          label="name"
          track-by="name"
        ></multiselect>
        {{ model.customer }}
        <br /><br />

        <span>vanaf datum contract</span>
        <input
          type="date"
          v-model="model.contractDateFrom"
          required
          name="contractDateFrom"
        />

        {{ model.contractDateFrom }}
        <br /><br />

        <span>tot datum contract</span>
        <input
          type="date"
          v-model="model.contractDateTill"
          required
          name="contractDateTill"
        />
        {{ model.contractDateTill }}
        <br /><br />

        <field-messages name="name">
          <div slot="required">Naam is een verplicht veld</div>
        </field-messages>
        <field-messages name="company">
          <div slot="required">Bedrijf is een verplicht veld</div>
        </field-messages>
        <field-messages name="contractDateFrom">
          <div slot="required">Vanaf datum is een verplicht veld</div>
        </field-messages>
        <field-messages name="contractDateTill">
          <div slot="required">Tot datum is een verplicht veld</div>
        </field-messages>
      </validate>

      <button type="submit">Submit</button>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "title", "contacten"],
  name: "add-contract",
  components: {},
  data: function() {
    return {
      formstate: {},
      model: {
        name: "",
        category: "",
        customer: "",
        file: "",
        contacts: [],
        contractDateFrom: "",
        contractDateTill: "",
        daysBeforeNotification: []
      }
    };
  },
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setFiles");
    this.$store.dispatch("setContacts");
    this.$store.dispatch("setMasterData");
    this.$store.dispatch("setDaysBeforeNotification");
  },
  computed: {
    files() {
      return this.$store.getters.getFiles.map(({ fileId, path }) => {
        return { id: fileId, name: path };
      });
    },
    customers() {
      return this.$store.getters.getCustomers.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    contacts() {
      return this.$store.getters.getContacts.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    daysBeforeNotifications() {
      return this.$store.getters.getDaysBeforeNotification.map(
        ({ id, name }) => {
          return { id: id, name: name };
        }
      );
    },
    categories() {
      return this.$store.getters.getCategories.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    masterData() {
      return this.$store.getters.getMasterData;
    }
  },
  methods: {
    onSubmit: function() {
      if (this.formstate.$invalid) {
        // alert user and exit early
        return;
      }
      this.$store.dispatch("addContract", {
        name: this.model.name,
        category: this.model.category.id,
        customer: this.model.customer.id,
        file: this.model.file.id,
        contacts: this.model.contacts,
        contractDateFrom: this.model.contractDateFrom,
        contractDateTill: this.model.contractDateTill,
        daysBeforeNotification: this.model.daysBeforeNotification
      });
      // this.$store.dispatch("setContracts");
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.newContractModal {
  padding: 20px;
}
.multiselect {
  &__placeholder {
    width: 100%;
  }
}
</style>
